import * as THREE from 'three'
import { convertGeoToPixel } from '../mercator'
import {LineGeometry} from 'three/examples/jsm/lines/LineGeometry'
import {LineMaterial} from 'three/examples/jsm/lines/LineMaterial'
import {Line2} from 'three/examples/jsm/lines/Line2'

const proviceColor = 0x004892
const proviceBorderColor = 0x5ca4de

export const lineMaterial = new LineMaterial({ color: proviceBorderColor, linewidth: 1.6 })

class Map extends THREE.Object3D {
  constructor (geojson, mapScale, centerPos) {
    super()

    this.mapScale = mapScale
    this.centerPos = centerPos

    this.meshGroup = new THREE.Group()
    this.lineGeoGroup = new THREE.Group()
    this.add(this.meshGroup)
    this.add(this.lineGeoGroup)

    geojson.features.forEach(feature => {
      let shapes = []
      feature.geometry.coordinates.forEach((coord, index) => {
        const type = feature.geometry.type
        if (type === 'MultiPolygon') {
          shapes.push(this.getShape(coord[0]))
        } else if (type === 'Polygon') {
          shapes.push(this.getShape(coord))
        } else {
          console.error('more type')
        }
      })

      let geometry = new THREE.ShapeGeometry(shapes)

      let meshMaterial = new THREE.MeshBasicMaterial({color: proviceColor})

      let mesh = new THREE.Mesh(geometry, meshMaterial)

      this.meshGroup.add(mesh)
    })
  }

  getShape = (arr) => {
    const shape = new THREE.Shape()
    let positions = []
    // startpoint
    const zBias = 0.01
    let [x, y] = convertGeoToPixel(arr[0], this.mapScale, this.centerPos)
    shape.moveTo(x, y)
    positions.push(x, y, zBias)

    for (var i = 1; i < arr.length; i++) {
      let [x, y] = convertGeoToPixel(arr[i], this.mapScale, this.centerPos)
      shape.lineTo(x, y)
      positions.push(x, y, zBias)
    }

    let lineGeometry = new LineGeometry().setPositions(positions)
    let line2 = new Line2(lineGeometry, lineMaterial)

    this.lineGeoGroup.add(line2)

    return shape
  }

  dispose = () => {

  }
}

export default Map
