import React from 'react'
import ReactDOM from 'react-dom'
import ThreeJsMap from './ThreeJsMap'
import data from './constant/data'
import chinaGeojson from './assets/chinaGeojson.json'

export default class Map extends React.Component {
  static defaultProps = {
    mapImage: 'https://cec-1256174840.cos.ap-shanghai.myqcloud.com/china_mapx2.png',
    data,
    enableDebug: false,
    geojson: chinaGeojson,
    option: {
      speed: 0.015,
      lineWidth: 0.22
    },
    optionSub: {
      speed: 0.006,
      lineWidth: 0.14
    }
  }

  componentDidMount () {
    const props = this.props
    let root = ReactDOM.findDOMNode(this)
    this.three = new ThreeJsMap(root, root.offsetWidth, root.offsetHeight, {...props})
  }

  componentWillUnmount () {
    this.three.dispose()
    this.three = null
  }

  render () {
    const props = this.props
    return (
      <div style={props.style} className={props.className} />
    )
  }
}
