import * as THREE from 'three'
import CameraController from '../../lib3d/cameraController'
import Flashing from '../../lib3d/instance/flashing'
import Glimmer from '../../lib3d/instance/glimmer'
import {
  calcCenterPos,
  calcMapScale,
  convertGeoToPixel,
  findBoundBoxFromGeojson
} from '../../lib3d/mercator'
import MapTexture from '../../lib3d/instance/mapTexture'
import MapArea, {lineMaterial} from '../../lib3d/instance/mapArea'
import { clearThree } from '../../lib3d/utils'

class ThreeJsMap {
  constructor (htmlRoot, width = 800, height = 800, {mapImage, data, geojson, enableDebug, option, optionSub}) {
    // 中国地图中心经纬度转网络墨卡托，用于移动转换后的地图到中心
    let boundBox = findBoundBoxFromGeojson(geojson)
    const centerPos = calcCenterPos([boundBox.centLng, boundBox.centLat])
    // 缩放因子,将地图最后 适应 100x100 的网格坐标
    const mapScale = calcMapScale([boundBox.maxLng, boundBox.maxLat], [boundBox.centLng, boundBox.centLat])

    this.clock = new THREE.Clock()
    this.sceneMap = new THREE.Scene()
    this.scene = new THREE.Scene()

    if (enableDebug) {
      lineMaterial.resolution.set(width, height) // important, for now...
      const mapArea = new MapArea(geojson, mapScale, centerPos)
      this.sceneMap.add(mapArea)
      mapArea.position.z = -4
    }

    let map = new MapTexture(mapImage)
    this.sceneMap.add(map)

    this.cameraController = new CameraController(width, height, htmlRoot, enableDebug)

    this.renderer = new THREE.WebGLRenderer({ antialias: true, alpha: true, precision: 'highp' })
    this.renderer.setPixelRatio(Math.max(window.devicePixelRatio, 2))
    this.renderer.setSize(width, height)
    htmlRoot.appendChild(this.renderer.domElement)

    let targetPos = convertGeoToPixel(data.target.coord, mapScale, centerPos)
    let targetVec = new THREE.Vector3(targetPos[0], targetPos[1], 0)

    let glimmer = new Glimmer(parseInt(data.target.color, 16), true)
    glimmer.position.copy(targetVec)
    this.scene.add(glimmer)
    glimmer.position.z = 8
    glimmer.scale.set(1.6, 1.6, 1)

    data.source.forEach(v => {
      const color = parseInt(v.color, 16)
      let startPos = convertGeoToPixel(v.coord, mapScale, centerPos)
      let startVec = new THREE.Vector3(startPos[0], startPos[1], 0)

      this.scene.add(new Flashing(startVec, targetVec, v, option))

      let glimmer = new Glimmer(color, false)
      glimmer.position.copy(startVec)
      this.scene.add(glimmer)
      glimmer.position.z = 0.02

      if (Array.isArray(v.children) && v.children.length) {
        let mergeGeometry = new THREE.Geometry()
        v.children.forEach(child => {
          let startPos = convertGeoToPixel(child.coord, mapScale, centerPos)
          let startVecSub = new THREE.Vector3(startPos[0], startPos[1], 0)
          this.scene.add(new Flashing(startVecSub, startVec, child, { ...optionSub, isSub: true }))

          let crcleGeometry = new THREE.CircleGeometry(0.32)
          let matrix = new THREE.Matrix4()
          matrix.setPosition(new THREE.Vector3().copy(startVecSub).setZ(2))
          mergeGeometry.merge(crcleGeometry, matrix)
        })

        this.scene.add(new THREE.Mesh(mergeGeometry, new THREE.MeshBasicMaterial({
          color: parseInt(v.children[0].color, 16)
        })))
      }
    })

    const animate = () => {
      const delta = this.clock.getDelta()

      this.cameraController.updateCamera(delta)

      let camera = this.cameraController.getCamera()

      this.renderer.autoClear = true
      this.renderer.render(this.sceneMap, camera)

      this.renderer.autoClear = false
      this.renderer.render(this.scene, camera)

      this.timerKey = window.requestAnimationFrame(animate)
    }

    animate()
  }

  // resize = (width, height) => {
  //   this.cameraController.resizeCamera(width, height)
  //   this.renderer.setSize(width, height)
  //   lineMaterial.resolution.set(width, height) // important, for now...
  // }

  dispose = () => {
    clearThree(this.sceneMap)
    clearThree(this.scene)
    this.renderer.dispose()
    this.cameraController.dispose()
    window.cancelAnimationFrame(this.timerKey)
  }
}

export default ThreeJsMap
