import * as THREE from 'three'
import loadTexture from '../loader/textureLoader'
import {getAb} from '../math'
import anime from 'animejs'
import flashWhite from '../assets/flash_white.png'

const flashSegment = 4

const [a, b] = getAb(4, 4, 50, 18)

const defaultConfig = {
  color: '0xffffff',
  curvature: 1,
  speed: 0.015,
  lineWidth: 0.20,
  lineFlashingRatio: 0.6
}

class Flashing extends THREE.Object3D {
  constructor (v1, v2, config, globalOption) {
    super()
    this.config = {
      ...defaultConfig,
      ...globalOption,
      ...config
    }

    this.lineWidth = this.config.lineWidth
    this.lineHeight = (this.config.isSub ? 32 : 64) * this.lineWidth

    this.v1 = v1
    this.v2 = v2
    this.index = 0
    this.curves = []

    this.dir = new THREE.Vector3().subVectors(v2, v1).normalize()

    let distance = v1.distanceTo(v2)

    this.cross = new THREE.Vector3()
    this.cross.crossVectors(this.dir, new THREE.Vector3(0, 0, 1))
    this.cross.normalize()

    let {lightingMaterial, tubeMaterial} = getMats(this.config.color)

    loadTexture(flashWhite).then((map) => {
      lightingMaterial.map = map

      this.curves.push(this.drawLine(distance, tubeMaterial, this.config.curvature))

      const loop = () => {
        this.timerKey = setTimeout(() => {
          this.emit(lightingMaterial)
          loop()
        }, Math.random() * 2000 + 2000)
      }

      loop()
    })
  }

  drawLine = (distance, tubeMaterial, curvature = 1) => {
    let dot = this.dir.dot(new THREE.Vector3(1, 0, 0)) > 0 ? -1 : 1

    const hOffset = distFun(distance)
    const crossOffset = distFun(distance)

    let curve = new THREE.QuadraticBezierCurve3(
      this.v1,
      new THREE.Vector3((this.v1.x + this.v2.x) * 0.5, (this.v1.y + this.v2.y) * 0.5, hOffset * curvature).addScaledVector(this.cross, dot * crossOffset * curvature),
      this.v2
    )

    const seg = Math.round(Math.max(4, a * distance + b))
    let tubeGeometry = new THREE.TubeGeometry(curve, seg, this.lineWidth * this.config.lineFlashingRatio, 4)

    let tubeMesh = new THREE.Mesh(tubeGeometry, tubeMaterial)

    this.add(tubeMesh)

    return {
      curve,
      curveLength: curve.getLength()
    }
  }

    emit = (lightingMaterial) => {
      this.index++

      const {curve, curveLength} = this.curves[this.index % this.curves.length]

      const lineWidth = this.lineWidth
      const lineHeight = this.lineHeight
      let planemesh = getPlane(lightingMaterial, lineWidth, lineHeight)

      const ratio = lineHeight / curveLength
      let coord = {x: 0}
      let delta = ratio / flashSegment
      const zBias = 0.01

      anime({
        targets: coord,
        x: 1 + ratio,
        easing: 'linear',
        duration: curveLength / this.config.speed,
        begin: () => {
          this.add(planemesh)
        },
        update: () => {
          let position = planemesh.geometry.attributes.position
          let index = 0
          let array = position.array

          let cur = coord.x + delta
          for (let i = 0; i < position.count / 2; i++) {
            cur = cur - delta

            const curveP = curve.getPoint(cur < 0 ? 0 : cur)

            array[index++] = curveP.x - this.cross.x * lineWidth
            array[index++] = curveP.y - this.cross.y * lineWidth
            array[index++] = curveP.z + zBias

            array[index++] = curveP.x + this.cross.x * lineWidth
            array[index++] = curveP.y + this.cross.y * lineWidth
            array[index++] = curveP.z + zBias
          }
          planemesh.geometry.attributes.position.needsUpdate = true
        },
        complete: () => {
          this.remove(planemesh)
          recyclePlane(planemesh)
        }
      })
    }

  dispose = () => {
    clearTimeout(this.timerKey)
  }
}

let COLOR_MATS = {}
// 曲线控制点，偏移量函数（手动调试出来）
function distFun (x) {
  let powParam = 1.8
  return Math.max(Math.min(Math.pow(x, powParam) * 18 / Math.pow(50, powParam), 18), 0.8)
}

function getMats (color) {
  if (!COLOR_MATS[color]) {
    COLOR_MATS[color] = {
      lightingMaterial: new THREE.MeshBasicMaterial({
        color: parseInt(color, 16),
        depthWrite: false,
        transparent: true
      }),
      tubeMaterial: new THREE.MeshBasicMaterial({
        color: parseInt(color, 16),
        opacity: 0.6,
        depthWrite: false,
        transparent: true
      })
    }
  }

  return COLOR_MATS[color]
}

let cache = []
function getPlane (mat, lineWidth, lineHeight) {
  if (cache.length === 0) {
    let planeBufferGeometry = new THREE.PlaneBufferGeometry(lineWidth, lineHeight, 1, flashSegment)
    return new THREE.Mesh(planeBufferGeometry, mat)
  } else {
    let plane = cache.pop()
    plane.material = mat
    return plane
  }
}

function recyclePlane (obj) {
  cache.push(obj)
}

export default Flashing
